/* Start Form Styles */
.ti-form-input {
  @apply  border border-inputborder block w-full py-[0.375rem] px-[0.75rem] rounded-sm text-sm focus:border-gray-200  focus:shadow-sm dark:shadow-white/10 dark:bg-bodybg dark:border-white/10 dark:focus:border-white/10 dark:text-white/70;
}

