.ti-switch {
  @apply relative h-[1.188rem] w-[2.25rem] bg-gray-200 border border-defaultborder dark:border-defaultborder/10 checked:bg-none checked:bg-primary  rounded-full cursor-pointer transition-colors ease-in-out duration-200 ring-0 ring-transparent focus:border-primary focus:ring-transparent ring-offset-white focus:outline-0 appearance-none dark:bg-bodybg dark:checked:bg-primary dark:focus:ring-offset-white/10 before:inline-block before:w-[calc(1.25rem-4px)] before:h-[calc(1.25rem-4px)] before:bg-white checked:before:bg-white  before:translate-x-0 ltr:checked:before:translate-x-full rtl:checked:before:-translate-x-full before:shadow before:rounded-full before:transform before:ring-0 before:transition before:ease-in-out before:duration-200 dark:before:bg-black/20 dark:checked:before:bg-black/20;
}
.ti-switch {
  @apply checked:focus:ring-transparent #{!important};
}
.ti-form-select {
  @apply py-[0.375rem] px-[0.75rem] pe-9 block w-full border-gray-200 rounded-sm text-sm focus:border-primary focus:ring-primary dark:dark:bg-bodybg dark:border-white/10 dark:text-white/70;
}

.ti-form-select-label {
  @apply block text-sm font-medium mb-2 dark:text-white;
}

.ti-form-label {
  @apply block text-sm font-medium mb-2 dark:text-white;
}

.ti-form-control {
  @apply border-inputborder text-defaulttextcolor bg-white dark:bg-bodybg2 dark:border-white/10 text-[0.875rem] font-normal leading-[1.6] rounded-[0.35rem] py-2 px-[0.85rem] placeholder:text-defaulttextcolor dark:placeholder:text-defaulttextcolor/70;

  &:focus {
    @apply shadow-none border-inputborder bg-white dark:bg-bodybg text-defaulttextcolor dark:text-defaulttextcolor/70;
  }
}
.form-select {
  @apply block w-full py-[0.375rem] pr-[2.25rem] pl-[0.75rem] text-[1rem] font-normal leading-[1.5];
}
.form-select {
  @apply bg-white dark:bg-bodybg border border-solid border-inputborder text-defaulttextcolor text-defaultsize rounded-md focus:border focus:border-primary;
  option {
    @apply bg-white dark:bg-bodybg py-[0.35rem] px-3 rounded-sm border border-primary/10;
  }

  option:checked {
    @apply bg-primary/20 text-primary;
  }
}


.form-check-input {
  @apply h-[0.9rem] w-[0.9rem] bg-white dark:bg-bodybg dark:border-white/10 border border-solid border-inputborder rounded-sm;
  &:focus{
    @apply border border-defaultborder outline-none #{!important};
  }

  &:checked {
    @apply bg-primary border-primary;
  }

  &.form-checked-outline {
    &:checked {
      @apply bg-transparent border-primary;
    }
  }

  &.form-checked-secondary {
    &:checked {
      @apply bg-secondary border-secondary hover:bg-secondary;
    }
  }

  &.form-checked-warning {
    &:checked {
      @apply bg-warning border-warning hover:bg-warning;
    }
  }

  &.form-checked-info {
    &:checked {
      @apply bg-info border-info hover:bg-info;
    }
  }

  &.form-checked-success {
    &:checked {
      @apply bg-success border-success;
    }
  }

  &.form-checked-danger {
    &:checked {
      @apply bg-danger border-danger hover:bg-danger;
    }
  }

  &.form-checked-light {
    &:checked {
      @apply bg-light border-light hover:bg-light;
    }
  }

  &.form-checked-dark {
    &:checked {
      @apply bg-[#232323] border-[#232323] hover:bg-black;
    }
  }

}
.form-label {
 @apply mb-3 #{!important};
}
[type='radio'] {
  @apply dark:bg-bodybg dark:border-defaultborder/10 ring-primary;
}

