/* Start IconText-Menu Styles */
[data-vertical-style="icontext"][data-toggled="icon-text-close"] {
  @media screen and (min-width: 992px) {
    &:not([data-icon-text="open"]) {
      .app-sidebar {
        @apply w-[9.5rem] #{!important};
        .badge{
          @apply hidden #{!important};
        }
        .simplebar-mask {
          @apply mt-0 #{!important};
        }
        .slide__category,
        .side-menu__angle {
          @apply hidden;
        }
        .side-menu__item {
          @apply block text-center text-xs px-0 #{!important};
          .side-menu__icon {
            @apply mx-auto mb-2 w-full block;
          }
        }
        .slide.has-sub {
          .slide-menu {
            @apply hidden px-0 #{!important};
          }
        }
        .app-sidebar .slide {
          @apply px-[0.25rem] #{!important};
        }
        .main-sidebar-header {
          @apply w-[9.5rem] #{!important};
          .header-logo {
            .desktop-logo,
            .toggle-logo,.desktop-white{
              @apply hidden;
            }
            .desktop-dark {
              @apply block;
            }
            .toggle-dark {
              @apply hidden;
            }
          }
        }
        .main-menu {
          @apply px-0 pt-[12px];
        }
      }
      &.dark {
        .app-sidebar {
          .main-sidebar-header {
            .header-logo {
              .toggle-logo,.toggle-dark {
                @apply hidden;
              }
              .desktop-dark {
                @apply block;
              }
            }
          }
        }
      }
    }
    &[dir="ltr"] {
      .content {
        @apply ml-[9.5rem];
      }
      .app-header {
        @apply pl-[9.5rem];
      }
    }
    &[dir="rtl"] {
      .content {
        @apply mr-[9.5rem];
      }
      .app-header {
        @apply pr-[9.5rem];
      }
    }
  }
}
/* End IconText-Menu Styles */
